@import url(https://fonts.google.com/css2?family=Monsterrate&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

*{
  box-sizing: border-box;
  margin:0;
  padding: 0;
  font-family: 'Monsterrate', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff
}

.coin-search{
  margin-bottom: 64px;
  display:flex;
  flex-direction: column;
  align-items: center;
}

.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  background-image: linear-gradient(
    -225deg, 
    #ac32e4 0%,
    #7918f2 40%,
    #4801ff 100%,
  );

  color: #e2e2e2;

}

.coin-input::-webkit-input-placeholder {
  color: e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: e2e2e2;
}

.coin-input::placeholder {
  color: e2e2e2;
}
.coin-cointainer {
    display:flex;
    justify-content: center;

}

.coin-row{
    display:flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7dd;
    width: 95%;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;

}

.coin > h1 {
    font-size: 16px;
    width: 150px
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-data{
    display:flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;

}

.coin-image{
    height: 30px;
    padding-right: 15px;
}

.coin-price{
    width: 110px;
}

.coin-volume{
    width: 230px;
}

.coin-market-cap{
    padding-left: 155px;
}

.coin-percent{
    width: 100%;
}

.red{
    color: #f00606;
}

.green{
    color: #11d811
}

