.coin-cointainer {
    display:flex;
    justify-content: center;

}

.coin-row{
    display:flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7dd;
    width: 95%;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;

}

.coin > h1 {
    font-size: 16px;
    width: 150px
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-data{
    display:flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;

}

.coin-image{
    height: 30px;
    padding-right: 15px;
}

.coin-price{
    width: 110px;
}

.coin-volume{
    width: 230px;
}

.coin-market-cap{
    padding-left: 155px;
}

.coin-percent{
    width: 100%;
}

.red{
    color: #f00606;
}

.green{
    color: #11d811
}
